.section__title {
  margin: 0;
  margin-bottom: 50px;
  font-weight: 400;
  font-size: 40px;
  font-family: "Alice", serif;
  text-align: center;

  @media (max-width: 940px) {
    margin-bottom: 35px;
    font-size: 30px;
  }

  @media (max-width: 670px) {
    margin-bottom: 25px;
    font-size: 20px;
  }
}
